<!--
 * @Description:前端实现分页效果 页面
 * @Author: mhf
 * @Date: 2023-05-13 11:58:19
-->
<template>
  <back-home-btn>
    <template>
      <el-table
        :data="tableData.slice((pageNo - 1) * pageSize, pageNo * pageSize)"
        style="width: 100%; margin-top: 20px"
        stripe
        border
      >
        <el-table-column
          align="center"
          type="index"
          :index="indexMethod"
          width="100px"
          label="序号"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="socialCode"
          width="200"
          label="社会统一信用代码"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="entName"
          label="企业名称"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="mistakeDate"
          label="错误时间"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="mistakeCause"
          label="错误原因"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="countyName"
          width="120"
          label="区县"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="villagesName"
          label="归属乡镇"
        >
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          align="center"
          prop="state"
          width="120"
          label="核查标识"
        >
          <template slot-scope="scope">
            <el-tag
              :type="
                scope.row.state === '0'
                  ? 'danger'
                  : scope.row.state === '1'
                  ? 'success'
                  : 'info'
              "
            >
              {{
                scope.row.state === "0"
                  ? "未核查"
                  : scope.row.state === "1"
                  ? "已核查"
                  : ""
              }}
            </el-tag>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="text-align: right; margin-top: 15px"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-sizes="[10, 20, 30, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </template>
  </back-home-btn>
</template>

<script>
import { mockTable } from "@/api/allRequset";

export default {
  name: "tablePaging",
  components: {},
  props: {},
  computed: {},
  filters: {},
  watch: {},
  data() {
    return {
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
    };
  },
  methods: {
    getTableData() {
      mockTable().then((res) => {
        this.tableData = res.data.data;
        this.total = this.tableData.length;
      });
    },

    handleSizeChange(val) {
      this.pageNo = 1;
      this.pageSize = val;
    },

    handleCurrentChange(val) {
      this.pageNo = val;
    },

    indexMethod(index) {
      return index + this.pageSize * (this.pageNo - 1) + 1;
    },
  },
  created() {
    this.getTableData();
  },
  mounted() {},
};
</script>

<style lang="scss" scoped></style>
